.step-1 {
  max-width: 900px;
  padding: 50px 100px;
  @include r(767) {
    padding: 0;
    padding-top: 40px; }
  h2 {
    font-size: 18px;
    padding-left: 20px;
    letter-spacing: 1.8px;
    color: $black;
    font-weight: normal;
    margin-bottom: 20px;
    @include r(767) {
      padding-left: 0;
      margin-bottom: 10px; } }
  h3 {
    font-size: 14px;
    color: $black;
    font-weight: normal; }
  &__content {
    padding: 45px 0;
    display: flex;
    @include r(767) {
      flex-direction: column; } }
  &__block {
    max-width: 185px;
    width: 100%;
    position: relative;
    margin-right: 50px;
    border-radius: 5px;
    border: solid 1px #fad0dc;
    @include r(767) {
      max-width: calc(100% - 10px);
      margin-bottom: 65px; }
    &--present {
      max-width: 240px;
      margin-right: 0;
      background: $very-light-purple;
      padding: 22px 35px;
      @include r(767) {
        padding-bottom: 50px; }
      img {
        @include r(767) {
          top: 0 !important; } }
      .step-1__block__header__content {
        @include r(767) {
          margin-top: 10px; } }
      span {
        font-size: 24px;
        color: $black;
        display: block;
        text-align: center;
        margin-top: 15px; } }
    .i {
      top: -15px;
      right: -15px; }
    h4 {
      font-size: 20px;
      color: $black;
      text-transform: uppercase;
      text-align: center;
      font-weight: normal;
      margin-bottom: 10px;
      @include r(767) {
        margin-bottom: 0; } }
    &__select {
      margin-top: 15px; }
    &__header {
      background: $pale-grey;
      border-radius: 5px;
      padding: 30px;
      &__content {
        @include center;
        img {
          @include r(767) {
            position: relative;
            top: -20px; } }
        p {
          font-size: 11px;
          color: $black;
          margin-left: 10px;
          @include r(767) {
            margin-left: 15px; } } } }
    &__footer {
      padding-top: 20px;
      padding-bottom: 40px;
      p {
        font-size: 11px;
        color: $black;
        display: block;
        text-align: center; }
      span {
        font-size: 24px;
        color: $black;
        display: block;
        text-align: center; }
      b {
        color: $warm-pink;
        font-size: 9px;
        @include center;
        align-items: flex-end;
        font-weight: 400;
        s {
          font-size: 14px;
          font-weight: 300;
          padding-left: 3px;
          line-height: 1; } } }
    .btn {
      position: absolute;
      bottom: -30px;
      left: 50%;
      transform: translate(-50%);
      font-size: 14px;
      font-weight: 500;
      padding: 20px 30px; } } }
