.info {
  padding-top: 45px;
  @include r(1040) {
    padding-top: 110px; }
  @include r(767) {
    padding-top: 40px; }
  &__content {
    padding-bottom: 95px;
    @include r(767) {
      padding-bottom: 35px; } }
  &__video {
    @include center;
    justify-content: flex-start;
    text-align: left;
    margin-bottom: 45px;
    font-size: 14px;
    font-weight: 500;
    color: $steel-grey;
    &:hover {
      i {
        color: #b5b9c5; } }
    @include r(767) {
      justify-content: center;
      margin-bottom: 35px; }
    i {
      font-size: 60px;
      color: $steel-grey;
      margin-right: 15px;
      @include tr; } }
  h2 {
    font-size: 44px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: 4.4px;
    text-align: right;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 45px;
    @include r(767) {
      text-align: center;
      font-size: 21px;
      margin-bottom: 25px; } }
  &__block {
    border-radius: 5px;
    background-color: $white;
    box-shadow: 0 20px 43px 0 rgba(230, 233, 240, 0.62);
    padding: 10px;
    position: relative;
    width: calc(100% - 15px);
    margin-right: 15px;
    @include r(767) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 30px; }
    &__content {
      @include r(767) {
        height: 100%; }
      &--secondary {
        @include r(767) {
          p {
            padding-bottom: 40px !important; } } }
      p {
        border-radius: 5px;
        border: solid 1px #fad0dc;
        font-size: 18px;
        @include center;
        color: $black;
        height: 75px;
        text-align: center;
        font-weight: normal;
        @include r(767) {
          font-size: 14px;
          height: 100%;
          padding: 15px 11px; } }
      a.i {
        top: 55%;
        transform: translate(0, -50%);
        right: -5px;
        @include r(767) {
          transform: none;
          top: -7px;
          right: 20px; } }
      .btn {
        position: absolute;
        left: 50%;
        transform: translate(-50%);
        bottom: -23px;
        font-size: 14px;
        width: 89%;
        @include center;
        padding: 20px 0; } }
    &__details {
      position: absolute;
      bottom: -105px;
      left: 54%;
      transform: translate(-50%);
      width: 315px;
      background: #E9EBF2;
      @include center;
      padding: 10px;
      border-radius: 3px;
      z-index: 2;
      opacity: 0;
      visibility: hidden;
      @include tr;
      &.active {
        opacity: 1;
        visibility: visible; }
      &:before {
        content: '';
        position: absolute;
        top: -7.5px;
        right: 50px;
        border-left: 8px solid transparent;
        border-bottom: 8px solid #E9EBF2;
        border-right: 8px solid transparent;
        display: block; }
      &__img {
        height: 100px;
        width: 100px;
        border-radius: 3px;
        overflow: hidden;
        img {
          @include object-fit(cover);
          width: 100%;
          height: 100%; } }
      &__text {
        padding-left: 16px;
        flex: 1;
        p {
          font-size: 11px;
          font-weight: normal;
          font-style: italic;
          color: $gunmetal;
          margin-bottom: 10px; } } } } }
